@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;700&display=swap");
/* ../public/assets/login/Login-BG-2.jpg */
html,
body,
#root,
.app,
.content {
  height: 100%;
  width: 100%;
  font-family: "Montserrat", montserrat;
  background-image: url("/src/assets/login/Logged-in-BG.jpg");
  background-repeat: repeat-y;
  background-size: cover;
  background-position: center center;
}
.bg {
  background-color: aqua;
}
.app {
  display: flex;
  position: relative;
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #e0e0e0;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.bold-column--cell {
  color: black; 
  font-weight: bold;
  font-size: small;
}
